<template>
  <div>
    <!--  -->
    <el-breadcrumb separator=">">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="querInfo.query" clearable @close="initOrdersList">
            <el-button slot="append" icon="el-icon-search" @click="initOrdersList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table
        :data="orderList"
        border
        stripe
        style="width: 100%"
        >
        <el-table-column
          type="index"
          label="#"
          width="50">
        </el-table-column>
        <el-table-column
          prop="order_number"
          label=""
          width="500">
        </el-table-column>
        <el-table-column
          prop="order_price"
          label=""
          width="60">
        </el-table-column>
        <el-table-column
          label=""
          width="100">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.order_pay === '0'"></el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="60">
          <template slot-scope="scope">
            {{ scope.row.is_send }}
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="180">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="140">
            <template>
              <el-tooltip class="item" effect="dark" content="" placement="top"><el-button type="primary" icon="el-icon-edit" size="small" @click="showReviseAddressDialog"></el-button></el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top"><el-button type="success" icon="el-icon-location" size="small" @click="showProgressDialog"></el-button></el-tooltip>
            </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="querInfo.pagenum"
        :page-sizes="[5,10,15,20]"
        :page-size="querInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        background
        :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="reviseAddressDialogVisible"
      width="30%" @close="reviseDialogClosed">
      <span>
        <el-form :model="reviseAddressForm" :rules="reviseAddressRules" ref="reviseAddressRef" label-width="100px">
          <el-form-item label="/" prop="address1">
            <el-cascader v-model="reviseAddressForm.address1" :options="cityData"></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address2">
            <el-input v-model="reviseAddressForm.address2"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="reviseAddressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="reviseAddressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressDialogVisible"
      width="30%">
      <span>
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in progressInfo"
            :key="index"
            :timestamp="activity.time">
            {{activity.context}}
          </el-timeline-item>
        </el-timeline>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="progressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="progressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata.js'
export default {
  data () {
    return {
      orderList: [],
      querInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      // 
      reviseAddressDialogVisible: false,
      // 
      reviseAddressForm: {
        address1: [],
        address2: ''
      },
      // 
      reviseAddressRules: {
        address1: [
          { required: true, message: '/', trigger: 'change' }
        ],
        address2: [
          { required: true, message: '', trigger: 'change' }
        ]
      },
      cityData,
      // 
      progressDialogVisible: false,
      // 
      progressInfo: [
        {
          time: '2022-05-10 09:39:00',
          ftime: '2022-05-10 09:39:00',
          context: ',,',
          location: ''
        },
        {
          time: '2022-05-10 08:23:00',
          ftime: '2022-05-10 08:23:00',
          context: '[]  95338',
          location: ''
        },
        {
          time: '2022-05-10 07:32:00',
          ftime: '2022-05-10 07:32:00',
          context: ' []',
          location: ''
        },
        {
          time: '2022-05-10 02:03:00',
          ftime: '2022-05-10 02:03:00',
          context: '[], []',
          location: ''
        },
        {
          time: '2022-05-09 23:05:00',
          ftime: '2022-05-09 23:05:00',
          context: ' []',
          location: ''
        },
        {
          time: '2022-05-09 21:21:00',
          ftime: '2022-05-09 21:21:00',
          context: '[], []',
          location: ''
        },
        {
          time: '2022-05-09 13:07:00',
          ftime: '2022-05-09 13:07:00',
          context: ' ',
          location: ''
        },
        {
          time: '2022-05-09 12:25:03',
          ftime: '2022-05-09 12:25:03',
          context: '',
          location: ''
        },
        {
          time: '2022-05-09 12:22:24',
          ftime: '2022-05-09 12:22:24',
          context: 'HLA',
          location: ''
        },
        {
          time: '2022-05-08 21:36:04',
          ftime: '2022-05-08 21:36:04',
          context: '',
          location: ''
        }
      ]
    }
  },
  methods: {
    async initOrdersList () {
      const { data: res } = await this.$http.get('orders', { params: this.querInfo })
      if (res.meta.status !== 200) return this.$message.error('')
      this.orderList = res.data.goods
      this.total = res.data.total
    },
    handleSizeChange (newSize) {
      this.querInfo.pagesize = newSize
      this.initOrdersList()
    },
    handleCurrentChange (newPage) {
      this.querInfo.pagenum = newPage
      this.initOrdersList()
    },
    reviseDialogClosed () {
      this.$refs.reviseAddressRef.resetFields()
    },
    showReviseAddressDialog () {
      this.reviseAddressDialogVisible = true
    },
    showProgressDialog () {
      this.progressDialogVisible = true
    }
  },
  created () {
    this.initOrdersList()
  }
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}

.el-timeline {
  padding: 0 20px;
}
</style>
